.reviews__wrapper {
  position: relative;

  width: 320px;

  margin: 0 auto;
  margin-bottom: 86px;
  padding: 0 30px;

  &::after {
    content: "";

    position: absolute;
    left: 30px;
    bottom: -50px;

    width: 260px;
    height: 6px;

    background-image: url("../img/bg-zigzag-line.svg");
    background-position: 0 0;
    background-size: contain;
  }
}

.reviews__item {
  margin-bottom: 31px;
}

.reviews__title {
  margin: 0;
  margin-bottom: 24px;
  padding: 0;

  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 1.2;
  color: #000000;
}

.reviews__text {
  margin: 0;
  margin-bottom: 28px;
  padding: 0;

  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 2;
  color: @greyish-brown;
}

.reviews__author {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 700;
  line-height: 1.41;
  color: #000000;
}

.reviews__address {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: normal;
  line-height: 1.76;
  color: @greyish-brown;
}

.reviews__button-container {
  display: flex;

  margin-bottom: 38px;
}

.reviews__page-button {
  display: block;
  width: 130px;
  height: 52px;

  border: 1px solid #ececec;

  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.reviews__page-button--prev {
  background-image: url("../img/icon-left-arrow.svg");
}

.reviews__page-button--next {
  background-image: url("../img/icon-right-arrow.svg");
}

.reviews__button {
  padding: 14px 80px;
}

@media (min-width: @tablet-width) {
  .reviews {
    position: relative;

    display: flex;

    margin-bottom: 112px;

    background-image: url("../img/review@1x.jpg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 0 -50px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/review@2x.jpg");
      background-size: 350px auto;
    }
  }

  .reviews__wrapper {
    width: @tablet-width;

    margin: 0 auto;
    padding: 0 50px;
    padding-top: 75px;
    padding-bottom: 53px;

    &::after {
      left: 50px;
      bottom: -50px;

      width: 668px;
      height: 14px;
    }
  }

  .reviews__title {
    margin: 0;
    margin-bottom: 50px;
    padding: 0;

    font-size: 43px;
    line-height: 1.16;
  }

  .reviews__item {
    display: flex;
    flex-direction: column;
  }

  .reviews__text {
    margin: 0;
    margin-bottom: 57px;
    padding: 0;

    font-size: 17px;
    line-height: 1.76;
  }

  .reviews__author {
    font-size: 18px;
    line-height: 1.33;
  }

  .reviews__button-container {
    position: absolute;
    right: 51px;
    bottom: 45px;
  }

  .reviews__page-button {
    width: 87px;

    border: none;
  }

  .reviews__button {
    position: absolute;
    top: 78px;
    right: 50px;

    padding: 14px 38px;
  }
}

@media (min-width: @desktop-width) {
  .reviews {
    margin-bottom: 174px;

    background-position: 155px 0;
  }

  .reviews__wrapper {
    width: @desktop-width;

    margin: 0 auto;
    padding-top: 71px;
    padding-left: 225px;
    padding-right: 107px;

    &::after {
      left: 50px;
      bottom: -102px;

      width: 1050px;
      height: 21px;

      background-position: -27px;
    }
  }

  .reviews__title {
    margin-bottom: 46px;

    font-size: 53px;
    line-height: 1.08;
  }

  .reviews__text {
    margin: 0;
    margin-bottom: 41px;
    padding: 0;
    padding-right: 30px;
  }

  .reviews__button-container {
    position: absolute;
    right: 51px;
    bottom: 47px;
  }
}
