.production {
  width: 320px;

  margin: 0 auto;
  margin-bottom: 58px;
  padding: 0 30px;
}

.production__wrapper {
  box-shadow: 0 0 0 1px #ececec;

  &:hover,
  &:focus {
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.1);
  }

  &:active {
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.1);
  }
}

.production__title {
  position: relative;

  margin: 0;
  padding: 23px 0;
  padding-left: 90px;

  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 700;
  line-height: 1.41;
  color: #000000;

  &::after {
    content: "";

    position: absolute;
    top: 28px;
    left: 34px;

    width: 32px;
    height: 32px;

    background-image: url("../img/icon-video.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
  }
}

.production__video {
  position: relative;

  margin-bottom: 23px;

  &::after {
    content: "";

    position: absolute;
    top: 37px;
    left: 80px;

    width: 101px;
    height: 77px;

    background-image: url("../img/icon-play.svg");
    background-repeat: no-repeat;
    background-position: 0 0;

    cursor: pointer;
  }

  &:hover::after,
  &:focus::after {
    transform: scale(1.2);
  }

  &:active::after {
    transform: scale(0.8);
  }
}

.production__img {
  width: 260px;
  height: 150px;
}

.production__text {
  margin: 0;
  margin-bottom: 25px;
  padding-left: 34px;
  padding-right: 43px;

  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 13px;
  line-height: 1.85;
  color: @greyish-brown;
}

.production__button {
  margin-left: 34px;
  margin-bottom: 33px;
  padding: 14px 24px;
}

@media (min-width: @tablet-width) {
  .production {
    width: @tablet-width;

    margin: 0 auto;
    margin-bottom: 63px;
    padding: 0 50px;
  }

  .production__wrapper {
    display: flex;
    flex-direction: column;
  }

  .production__video {
    margin-bottom: 16px;

    &::after {
      top: 189px;
      left: 283px;

      width: 102px;
      height: 78px;
    }
  }

  .production__img {
    width: 668px;
    height: 455px;
  }

  .production__title {
    order: 10;

    margin: 0;
    margin-bottom: 8px;
    padding-left: 107px;

    font-size: 20px;
    line-height: 1.2;

    &::after {
      top: 18px;
      left: 43px;
    }
  }

  .production__text {
    order: 20;

    margin-bottom: 36px;
    padding-left: 40px;
    padding-right: 47px;

    font-size: 15px;
    line-height: 2;
  }

  .production__button {
    order: 20;

    margin: 0 42px;
    margin-bottom: 46px;
    padding: 14px 220px;
  }
}

@media (min-width: @desktop-width) {
  .production {
    position: relative;

    width: @desktop-width;

    margin: 0 auto;
    margin-bottom: 111px;
    padding: 0 50px;
    padding-left: 732px;
  }

  .production__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    padding-top: 42px;
    padding-left: 80px;
  }

  .production__video {
    position: absolute;
    top: 0;
    left: 50px;

    &::after {
      top: 189px;
      left: 291px;
    }
  }

  .production__img {
    width: 682px;
    height: 455px;
  }

  .production__title {
    margin: 0;
    margin-bottom: 33px;
    padding: 0;
    padding-top: 53px;
    padding-right: 140px;

    &::after {
      top: 0;
      left: 0;
    }
  }

  .production__text {
    margin-bottom: 47px;
    padding-left: 0;
    padding-right: 65px;
  }

  .production__button {
    margin: 0;
    margin-bottom: 54px;
    padding: 14px 43px;
  }
}
